body   
{ 
height: 92vh;  
margin-top: 25px;  
background-size: 100% 100%;  
font-family: sans-serif;  
} 
h1{  
margin-top: 55px;  
padding: 20px;  
font-size:75px; 
font-weight:bold;
font-family: sans-serif; 
}
header {  
position: fixed; 
left: 0;  
right: 0;  
top: 5px;  
height: 50px;  
justify-content: flex-end;
display: flex; 
align-items: center;  
box-shadow: 0 0 25px 0 black;  
}  
header * {  
display: inline;  
}  
header li {  
margin: 20px;  
}  
header li a {  
color: blue;  
text-decoration: none;  
} 
input {
  float: right;
  clear: both;
}
.page{
width: 30%;
}

    /* Footer styles */
    .footer {
      position: fixed;
      left: 0;
      bottom: 45px;
      width: 100%;
      height: 40px; /* Adjust the height as needed */
      text-align: center;
      font-weight:bold;

    }
    
button {
border-radius: 5px;
  padding: 8px 24px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

